@import "vars";
@import "fonts";

* {
    box-sizing: border-box; }

body {
    @include reg; }

img {
    max-width: 100%; }

.container {
    max-width: 1170px;
    width: 96%;
    margin: auto;
    @media (max-width: 640px) {
        width: 90%; } }

.button {
    background-color: $buttons;
    color: $accent;
    border: none;
    @include bold;
    cursor: pointer;
    padding: 18px 47px;
    border-radius: 10px;
    &:hover {
        background-color: lighten($buttons, 10%); } }

.input {
    border: 1px solid $border-input;
    border-radius: 10px;
    padding: 20px 25px;
    font-size: 15px; }

.header {
    padding: 20px 0;
    &-block {
        display: flex;
        justify-content: space-between;
        align-items: center; }
    &__info {
        display: flex;
        text-align: right; }
    &__contacts {
        display: flex;
        flex-direction: column;
        justify-content: center;
        @media  (max-width: 640px) {
            display: none; } }

    &__address {
        color: $accent;
        font-size: 13px;
        font-weight: 400; }
    &__phone {
        color: $accent;
        font-size: 20px;
        font-weight: 400;
        text-decoration: none;
        display: block;
        margin-top: 5px;
        @include bold; }
    &__button {
        padding: 18px 47px;
        border-radius: 10px;
        margin-left: 30px;
        font-size: 13 px; } }

.main {
    background-color: $grey;
    &-block {
        display: flex;
        padding: 40px 0 15 px 0;
        @media (max-width: 1200px) {
            flex-direction: column; } }
    &-text {
        flex-basis: 50%;
        @media (max-width: 1200px) {
            margin-bottom: 30px; }
        &__title {
            font-size: 41px;
            color: $accent;
            padding-right: 200px;
            line-height: 55px;
            @include bold;
            margin: 45px 0 20px 0;
            @media (max-width: 1200px) {
                padding-right: 10px; }
            @media (max-width: 640px) {
                font-size: 30px;
                line-height: 1.4; } }
        &__subtitle {
            font-size: 20px;
            line-height: 65px;
            color: $accent;
            @media (max-width: 640px) {
                font-size: 16px;
                line-height: 1.4; } } }
    &-image {
        margin-right: -55px;
        @media (max-width:1200px) {
            width: 100%; } }
    &-list {
        margin: 40px 0 0;
        padding: 0 0 0 0;
        @media (max-width: 640px) {
            margin: 30px 0 0; }
        &__item {
            display: flex;
            align-items: center;
            margin-bottom: 20px;
            &:last-child {
                margin-bottom: 0; }
            @media (max-width: 640px) {
                margin-bottom: 15px; } }
        &__image {
            width: 40px;
            height: 40px;
            border: 2px solid #ffd200;
            border-radius: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 30px; }
        &__text {
            line-height: 24px;
            font-size: 14px;
            color: $accent;
            @media (max-width: 640px) {
                font-size: 12px;
                max-width: 250px; } } } }


.offer {
    padding: 90px 0;
    background-image: url('../img/offer-bg.jpg');
    background-position: center right;
    background-size: contain;
    position: relative;
    @media (max-width: 1200px) {
        background-size: cover; }
    @media (max-width: 768px) {
        background: #fff; }
    &:before {
        content: "";
        width: 50%;
        height: 100%;
        position: absolute;
        background-color: #fff;
        top: 0;
        left: 0; }
    &-block {
        width: 50%;
        padding-right: 90px;
        position: relative;
        @media (max-width: 768px) {
            width: 100%;
            padding-right: 0; } }
    &-title {
        font-size: 24px;
        line-height: 35px;
        max-width: 350px;
        margin-bottom: 25px;
        @include bold; }
    &-subtitle {
        display: inline-block;
        max-width: 240px;
        font-size: 16px;
        margin-bottom: 30px; }
    &__input {
        margin-right: 15px;
        margin-bottom: 30px;
        width: 220px;
        @media (max-width: 1200px) {
            width: 100%;
            margin-bottom: 15px; }
        &:first-child {
            margin-right: 15px; } }
    &__button {
        width: 96.5%;
        min-height: 70px;
        @media (max-width: 1200px) {
            font-size: 13px; }
        @media (max-width: 768px) {
            font-size: 16px; } } }


